<template>
  <div id="my-favorites-component">
    <div class="recommend-zoom-container">
      <h2 class="zoom-title">Most Viewed</h2>
      <el-carousel height="150px" autoplay="false" arrow="always">
        <el-carousel-item
          v-for="(row, index) in formatBrandData(brandData)"
          :key="index"
        >
          <el-row :gutter="30">
            <el-col v-for="(item, _index) in row" :key="_index" :span="4">
              <div class="brand-card-container" @click="onCardClick(item.id)">
                <div
                  class="cover"
                  :style="{
                    backgroundImage: `url('${formatImageUrl(item.logo) +
                      '@100-100'}')`
                  }"
                ></div>
                <div
                  class="button-favorite-container"
                  @click.stop="onFavoriteClick(item.id, item.is_favorite)"
                >
                  <img
                    class="favorite-icon"
                    :src="item.is_favorite ? heartSolidIcon : heartIcon"
                    alt=""
                  />
                </div>
                <!--<div class="button-close-container" @click.stop="onCloseClick">-->
                <!--<img-->
                <!--class="close-icon"-->
                <!--src="@/assets/images/icons/png/close-black.png"-->
                <!--alt=""-->
                <!--/>-->
                <!--</div>-->
              </div>
            </el-col>
          </el-row>
        </el-carousel-item>
      </el-carousel>
    </div>

    <div class="favorites-bards-zoom-container">
      <h2 class="zoom-title">Favorite Brands</h2>
      <BrandTable
        max-height="500px"
        :data="tableData"
        @reload="onReload"
      ></BrandTable>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref, computed } from "vue";

import BrandTable from "@/components/BrandTable.vue";

import heartIcon from "@/assets/images/icons/png/heart.png";
import heartSolidIcon from "@/assets/images/icons/png/heart_solid.png";
import { useRouter } from "vue-router";
import {
  getBrandMostViewed,
  getBrandsTable,
  putBrandFavorite
} from "@/apis/brand";
import { BrandTableItemResponse } from "@/typings/brandInsight";
import { formatImageUrl, loading } from "@/utils";

const Component = defineComponent({
  name: "MyFavorites",
  components: {
    BrandTable
  },
  props: {
    data: {
      type: Object,
      default: () => ({})
    }
  },
  setup() {
    const router = useRouter();
    const brandData = ref<BrandTableItemResponse[]>([]);
    const tableData = ref<BrandTableItemResponse[]>([]);

    const formatBrandData = (data: BrandTableItemResponse[]) => {
      return data.reduce(
        (
          result: BrandTableItemResponse[][],
          it: BrandTableItemResponse,
          index: number
        ) => {
          if (index % 6) {
            const order = Math.floor(index / 6);
            result[order].push(it);
          } else {
            result.push([it]);
          }
          return result;
        },
        []
      );
    };

    const fetchMostViewed = async () => {
      const { data } = await getBrandMostViewed();
      brandData.value = data;
    };

    const fetchTable = async () => {
      const { data } = await getBrandsTable({ is_favorite: true });
      tableData.value = data;
    };

    const init = async () => {
      const loadingInstance = loading();
      try {
        await fetchMostViewed();
        await fetchTable();
      } finally {
        loadingInstance.close();
      }
    };

    onMounted(init);

    const onReload = init;

    const onCardClick = async (id: number) => {
      await router.push({ name: "BrandSummary", params: { id } });
    };

    const onFavoriteClick = async (id: number, is_favorite: boolean) => {
      await putBrandFavorite(id, !is_favorite);
      await init();
    };

    return {
      heartIcon,
      heartSolidIcon,
      brandData,
      tableData,

      onReload,
      onCardClick,
      onFavoriteClick,

      formatBrandData,
      formatImageUrl
    };
  }
});

export default Component;
</script>

