
import { defineComponent, onMounted, ref, computed } from "vue";

import BrandTable from "@/components/BrandTable.vue";

import heartIcon from "@/assets/images/icons/png/heart.png";
import heartSolidIcon from "@/assets/images/icons/png/heart_solid.png";
import { useRouter } from "vue-router";
import {
  getBrandMostViewed,
  getBrandsTable,
  putBrandFavorite
} from "@/apis/brand";
import { BrandTableItemResponse } from "@/typings/brandInsight";
import { formatImageUrl, loading } from "@/utils";

const Component = defineComponent({
  name: "MyFavorites",
  components: {
    BrandTable
  },
  props: {
    data: {
      type: Object,
      default: () => ({})
    }
  },
  setup() {
    const router = useRouter();
    const brandData = ref<BrandTableItemResponse[]>([]);
    const tableData = ref<BrandTableItemResponse[]>([]);

    const formatBrandData = (data: BrandTableItemResponse[]) => {
      return data.reduce(
        (
          result: BrandTableItemResponse[][],
          it: BrandTableItemResponse,
          index: number
        ) => {
          if (index % 6) {
            const order = Math.floor(index / 6);
            result[order].push(it);
          } else {
            result.push([it]);
          }
          return result;
        },
        []
      );
    };

    const fetchMostViewed = async () => {
      const { data } = await getBrandMostViewed();
      brandData.value = data;
    };

    const fetchTable = async () => {
      const { data } = await getBrandsTable({ is_favorite: true });
      tableData.value = data;
    };

    const init = async () => {
      const loadingInstance = loading();
      try {
        await fetchMostViewed();
        await fetchTable();
      } finally {
        loadingInstance.close();
      }
    };

    onMounted(init);

    const onReload = init;

    const onCardClick = async (id: number) => {
      await router.push({ name: "BrandSummary", params: { id } });
    };

    const onFavoriteClick = async (id: number, is_favorite: boolean) => {
      await putBrandFavorite(id, !is_favorite);
      await init();
    };

    return {
      heartIcon,
      heartSolidIcon,
      brandData,
      tableData,

      onReload,
      onCardClick,
      onFavoriteClick,

      formatBrandData,
      formatImageUrl
    };
  }
});

export default Component;
