<template>
  <div id="brand-table-component">
    <el-table
      :data="data"
      :style="{ width: '100%' }"
      :max-height="maxHeight"
      header-row-class-name="table-header"
      cell-class-name="table-cell"
      stripe
    >
      <el-table-column label="Brand" width="200px">
        <template #default="scope">
          <div class="brand-cell">
            <div class="button-container" @click="onFavoriteClick(scope.row)">
              <img
                class="favorite-icon icon"
                :src="scope.row.is_favorite ? heartSolidIcon : heartIcon"
                alt=""
              />
            </div>
            {{ scope.row.name }}
          </div>
        </template>
      </el-table-column>
      <el-table-column label="Instagram Follower (WoW)" width="250px">
        <template #default="scope">
          <div :class="getPercentageClass(scope.row.ig_follower_growth)">
            {{ getPercentageText(scope.row.ig_follower_growth) }}
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="google_trend_growth" label="Google Tread (MoM)" width="200px">
        <template #default="scope">
          <div :class="getPercentageClass(scope.row.google_trend_growth)">
            {{ getPercentageText(scope.row.google_trend_growth) }}
          </div>
        </template>
      </el-table-column>
      <el-table-column
        prop="google_trend_growth"
        label="Positive Business Outlook"
        width="300px"
      >
        <template #default="scope">
          <div>
            {{ getPercentageTextWithoutSign(scope.row.positive_business_outlook) }}
          </div>
        </template>
      </el-table-column>
      <el-table-column
        prop="google_trend_growth"
        label="Senior Management Rating"
        width="260px"
      >
        <template #default="scope">
          {{ getText(scope.row.senior_management_rating) }}
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType } from "vue";
import { useRouter } from "vue-router";

import heartIcon from "@/assets/images/icons/png/heart.png";
import heartSolidIcon from "@/assets/images/icons/png/heart_solid.png";
import uploadIcon from "@/assets/images/icons/png/upload.png";
import { BrandTableItemResponse } from "@/typings/brandInsight";
import { putBrandFavorite } from "@/apis/brand";

const Component = defineComponent({
  name: "BrandTable",
  props: {
    maxHeight: {
      type: String,
      default: "auto"
    },
    data: {
      type: Array as PropType<BrandTableItemResponse[]>,
      default: () => []
    }
  },
  setup(_, { emit }) {
    const onFavoriteClick = async (row: BrandTableItemResponse) => {
      await putBrandFavorite(row.id, !row.is_favorite);
      emit("reload");
    };

    const getPercentageClass = (value: number | null) => {
      if (typeof value !== "number") return "";
      return value >= 0 ? "positive" : "negative";
    };

    const getPercentageText = (value: number | null) => {
      if (typeof value !== "number") return "N/A";
      const isPositive = value >= 0;
      return `${isPositive ? "+" : ""}${(value * 100).toFixed(2)}%`;
    };

    const getPercentageTextWithoutSign = (value: number | null) => {
      if (typeof value !== "number") return "N/A";
      return `${(value * 100).toFixed(2)}%`;
    };

    const getText = (value: number | null) => {
      if (typeof value !== "number") return "N/A";
      return value.toFixed(1);
    };

    return {
      heartIcon,
      heartSolidIcon,
      uploadIcon,

      onFavoriteClick,

      getPercentageClass,
      getPercentageText,
      getPercentageTextWithoutSign,
      getText
    };
  }
});

export default Component;
</script>

